import React, { Fragment, useState, useEffect } from "react";
import Axios from "axios";
import './Blog.css';
let blog_posts = "";

async function get_blog_articles() {
  const apiKey = "41e896a0a1c94b61903408fae1a49471";

  const now = new Date();
  const this_year = now.getFullYear();
  const this_month = now.getMonth();
  const this_day = now.getDate();
  const this_date = this_year + "-" + this_month + "-" + this_day;

  const this_pagesize = 20;

  let results = "";

  const RequestUrl ="https://newsapi.org/v2/top-headlines?country=za&category=entertainment&apiKey=41e896a0a1c94b61903408fae1a49471";

  await Axios.get(RequestUrl).then(result => {
      if (result.status === 200) {
        return result.data;
      } else {
        throw new Error("There was an error fetching articles");
      }
    }).then(articles => {
      results = articles;
    }).catch(error => {
      console.log(error);
    });
  console.log("RESULTS : ", results);
  return results.articles;
}

function BlogHome() {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    async function fetchData() {
      // You can await here
      // ...
      let blog_posts = await get_blog_articles();
      console.log("BLOG POSTS : ", blog_posts);
      setPosts(blog_posts);
    }
    fetchData();
  }, []);

  return (
    <Fragment>
      <div className="box box-body">
        <div className="box-header">
          <h3 className="box-title">
            <strong>
              {" "}
              <i className="fa fa-file"> </i> Articles
            </strong>
          </h3>
        </div>
        <div className="row">
          {posts.length > 0
            ? posts.map((post, index) => {
                return <BlogPost post_data={post} key={index} />;
              })
            : ""}
        </div>
      </div>
    </Fragment>
  );
}

function BlogPost({ post_data }) {
  const [post, setPost] = useState({});

  let article = post_data;
  console.log("INNER ARTICLE", article);
  return (
    <Fragment>
      <div className="box box-body with-border">
        <div className="box box-header with-border">
          <a href={article.url} target="_blank">
            <h2 className="box-title">{article.title}</h2>
          </a>
        </div>
        <div className="polaroid">
          <img src={article.urlToImage} className="pola-image" />
          <div className="polatext">{article.description}</div>
          <div className="box box-footer with-border">
            <a href={article.url} target="_blank">
              <h3 className="box-title">
                {" "}
                {article.source.name} - {article.author}{" "}
              </h3>
            </a>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default function Blog() {
  return (
    <Fragment>
      <div className="box box-body">
        <div className="box box-header">
          <h3 className="box-title">
            <strong>
              {" "}
              <i className="fa fa-book"> </i> Blog{" "}
            </strong>
          </h3>

          <div className="box-tools">
            <button type="button" className="btn btn-box-tool" name="pages">
              <strong>
                {" "}
                <i className="fa fa-folder"> </i> Pages{" "}
              </strong>
            </button>
            <button type="button" className="btn btn-box-tool" name="posts">
              <strong>
                {" "}
                <i className="fa fa-file"> </i> Posts{" "}
              </strong>
            </button>
            <button
              type="button"
              className="btn btn-box-tool"
              name="categories"
            >
              <strong>
                {" "}
                <i className="fa fa-folder-open"> </i> Categories{" "}
              </strong>
            </button>
          </div>
        </div>

        <BlogHome />
      </div>
    </Fragment>
  );
}
